.Input {
    margin-bottom: 15px;
}

.Input label {
    text-transform: uppercase;
    font-weight: 600;
    color: #19456b;
    color: white;
    display: block;
}

.Input input,
.Input textarea,
.Input select {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    font-family: Arial, Helvetica, sans-serif;
    border-radius: 4px;
    border: 1px solid silver;
    margin-top: 5px;
}

.invalid {
    background: #f3c7c7;
    border: 1px solid #e45454;
}

.Input span {
    color: red;
}