.Spinner_loader__2DeuP {
  font-size: 10px;
  margin: 50px auto;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: #00818A;
  background: linear-gradient(to right, #00818A 10%, rgba(251,116,62, 0) 42%);
  position: relative;
  -webkit-animation: Spinner_load3__39awk 1.4s infinite linear;
  animation: Spinner_load3__39awk 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.Spinner_loader__2DeuP:before {
  width: 50%;
  height: 50%;
  background: #00818A;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.Spinner_loader__2DeuP:after {
  background: #283149;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes Spinner_load3__39awk {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes Spinner_load3__39awk {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
